.header {
  width: 100%;
  display: flex;
  justify-content: center;
}
.header nav {
  display: flex;
  justify-content: space-between;
  /* background: #f2f9ff; */
  width: 100%;
  max-width: 80rem;
  padding: 1rem 0rem;
}

.logo__container {
  display: flex;
  align-items: center;
  gap: 1rem;
  color: #000;
}

.logo {
  width: 3rem;
  max-width: 4rem;
}

.git__button svg {
  font-size: 1.6rem;
}

/* Responsive */

@media screen and (max-width: 768px) {
  .header {
    margin-bottom: 5rem;
  }
}
@media screen and (max-width: 480px) {
  .logo__container {
    justify-content: center;
    width: 100%;
  }
  .git__button {
    display: none;
  }
}
