.download__container {
  display: flex;
  align-items: center;
  width: 100%;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 0rem 0 5rem 0;
}

.download__container img {
  width: 8rem;
}

.download__container h1 {
  font-size: 3rem;
  margin: 2rem 0rem;
  text-align: center;
}

.download__container > button {
  font-size: 1rem;
}

.download__container > button > svg {
  font-size: 1.2rem;
}

.spinner__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 80vh;
}

/* Responsive */

@media screen and (max-width: 768px) {
  .download__container h1 {
    font-size: 2rem;
  }
  .download__container h2 {
    font-size: 1.2rem;
  }
  .download__container > ul > li {
    font-size: 1rem;
  }
}
