* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "DM Sans", sans-serif;
}

body {
  box-sizing: border-box;
  font-family: "DM Sans", sans-serif;
  padding: 0rem 4rem;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button:focus,
input:focus {
  outline: none;
}

/* Responsive */

@media screen and (max-width: 1200px) {
  body {
    padding: 0rem 3rem;
  }
}
@media screen and (max-width: 768px) {
  body {
    padding: 0rem 1.5rem;
  }
}

