.progress__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 2rem 0rem;
  padding: 1.5rem 2rem;
  width: 100%;
  max-width: 50.5rem;
  background: #f2f9ff;
  border-radius: 0.5rem;
}

.progress__container p {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 1rem;
  color: #222222;
  text-align: left;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.progress__bar {
  width: 100%;
  background: white;
  height: 0.5rem;
  border-radius: 5rem;
  margin-top: 1rem;
  position: relative;
}

.progress__bar > span {
  transition: width 0.3s ease-in;
  content: "";
  position: absolute;
  height: 0.5rem;
  background: #4aa1f3;
  border-radius: 5rem;
}
