.share__container {
  display: flex;
  justify-content: space-between;
  margin: 2rem 0rem;
  width: 100%;
  align-items: center;
  flex-wrap: wrap;
  max-width: 50rem;
  grid-gap: 2rem;
  gap: 2rem;
}

.link__wrapper {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.share__container > input {
  max-width: 100%;
  flex: 1;
  border: 1px solid lightskyblue;
  text-indent: 1rem;
  height: 3rem;
  font-size: 0.9rem;
  border-radius: 0.3rem;
}

.textbox__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: center;
  width: 100%;
}

.textbox__wrapper > input {
  max-width: 100%;
  flex: 1;
  border: 1px solid lightskyblue;
  text-indent: 1rem;
  height: 2.5rem;
  font-size: 0.9rem;
  border-radius: 0.3rem;
}
