.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  min-height: 40vh;
  justify-content: center;
}

.dropzone__wrapper {
  width: 100%;
  max-width: 50rem;
}

.drop__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 2rem;
  width: 100%;
  max-width: 50rem;
  border: 4px dashed #4aa1f3;
  border-radius: 0.5rem;
}

.drop__container > img {
  max-width: 5rem;
}

.drop__message {
  text-align: center;
  color: #222222;
  font-family: Arial;
  font-size: 20px;
  margin: 1.5rem 0rem;
}

.browse__btn {
  color: #4aa1f3;
}
